main{
    .community2 {
        width: 100%;
        height: 215vh;
        position: relative;
        top: 200px;
        display: block;
        // overflow: hidden;
        // opacity: 0;
    
        // &:hover{
        //     opacity: 1;
        // }

        
      
        h2{
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            top: 0;
            font: bold 50px/1 "arial";
        }
    
        .inner2 {
            width: 1180px;
            margin: 50px auto;
            padding: 0 20px;
            position: relative;
            // top: 10%;
    
            .center_line {
                position:absolute;
                top: 0%;
                left: 50%;
                transform: translateX(-50%);
                width: 4px;
                height: 110%;
                background-color: #000;
    
                .scroll_icon {
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    background-color: #f2f2f2;
                    font-size: 25px;
                    text-align: center;
                    line-height: 40px;
                    color: #79e5cb;
                    box-shadow: 0 0 0 4px #fff,
                        inset 0 2px 0 rgba(0, 0, 0, 0.08),
                        0 3px 0 4px rgba(0, 0, 0, 0.05);
                }
            }
    
            .row {
                display: flex;
                
    
                section {
                    background-color: #eee;
                    border-radius: 5px;
                    width: calc(50% - 40px);
                    padding: 20px;
                    position: relative;
                    top: 100px;
                    font: bold 20px/1 "arial";
                   opacity: 0;
                    transform: rotateY(180deg) translateY(100px);
                    transition: 0.5s;
            
                                &.on{
                                   
                                    opacity: 1;
                                    transform: rotateY(0deg) translateY(0px);
                                    
                                }
                   
                    .title{
                        
                        display: flex;
                        justify-content: center;
                        align-items: center;
    
                    }
    
                    .derails {
                        
                        justify-content: space-between;
                        align-items: center;
                        font: bold 14px/1 "arial"; 
    
                        
                            img{
                                position: relative;
                                
    
                                top: -5px;
                                width: 100px;
                                height: 100px;
                                padding: 5px 5px 5px 5px; //상우하좌
                                margin: 5px 5px 5px 5px;
                               
                               
                            }
        
                            p{
                                display: inline;
                                position: relative;
                                top: 10px;
                                left: -70%;
                                margin: 0px 5px 0px 5px;
                                white-space : nowrap; 
                            }
                        }
    
    
                    .bottom {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
    
                        a {
                            display: block;
                            background-color: #79e5cb;
                            padding: 7px 15px;
                            border-radius: 5px;
                            color: #fff;
                        }
                    }
    
                    &::before {
                        content: '';
                        display: block;
                        width: 15px;
                        height: 15px;
                        background-color:#79e5cb;
                        position: absolute;
                        top: 28px;
                        transform: rotate(45deg);
                        z-index: -1;
                    }
    
                    > i {
                        position: absolute;
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        background-color: #f2f2f2;
                        text-align: center;
                        line-height: 40px;
                        color: #79e5cb;
                        font-size: 17px;
                        box-shadow: 0 0 0 4px #fff,
                            inset 0 2px 0 rgba(0, 0, 0, 0.08),
                            0 3px 0 4px rgba(0, 0, 0, 0.05);
                    }
                }
    
                &.row_left {
                    justify-content: flex-start;
    
                    section::before {
                        right: -7px;
                    }
    
                    section > i {
                        top: 15px;
                        right: -60px;
                    }
                }
    
                &.row_right {
                    justify-content: flex-end;
    
                    section::before {
                        left: -7px;
                    }
    
                    section > i {
                        top: 15px;
                        left: -60px;
                    }
                }
            }
        }
    }
}


.scroll{
    display: none;
    position: fixed;
    right: 50px;
    top: 50%;
    transform: translateY(-50%);

    li{
        width: 20px;
        height: 20px;
        border-radius: 10px;
        background-color: #fff;
        margin-bottom: 20px;
        cursor: pointer;

        &.on{
            height: 80px;
            background-color: #000;
        }
    }

    
}








